/* Checkbox & Radio Styles */
input[type=checkbox],
input[type=radio] {
  visibility: hidden;
  opacity: 0;
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  display: none;
}

input[type=checkbox] ~ label,
input[type=radio] ~ label {
  position: relative;
  padding-left: 24px;
  cursor: pointer;
}

input[type=checkbox] ~ label:before,
input[type=radio] ~ label:before {
  content: "";
  font-family: 'Font Awesome 5 free';
  font-weight: 700;
  position: absolute;
  left: 0;
  top: 5px;
  border: 1px solid #dddddd;
  height: 15px;
  width: 15px;
  line-height: 1;
  font-size: 13px;
}

input[type=checkbox]:checked ~ label,
input[type=radio]:checked ~ label {
  color: #1cb9c8;
}

input[type=checkbox]:checked ~ label:before,
input[type=radio]:checked ~ label:before {
  content: "\f00c";
  color: #1cb9c8;
  border-color: #1cb9c8;
}

input[type=radio] ~ label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  border: 2px solid #dddddd;
  border-radius: 100px;
  height: 15px;
  width: 15px;
  margin-top: -8px;
  line-height: .5;
  font-size: 28px;
}

input[type=radio]:checked ~ label::before {
  content: "";
  color: #1cb9c8;
  border-color: #1cb9c8;
}

