/* Preloader Styels */
.tm-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background: rgba(28, 185, 200, 0.98);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.tm-preloader span {
  display: inline-block;
  height: 30px;
  width: 30px;
  background: 0 0;
  border-radius: 1000px;
  position: relative;
  z-index: 1;
}

.tm-preloader span::before, .tm-preloader span::after {
  content: "";
  left: 50%;
  top: 50%;
  position: absolute;
  display: inline-block;
  border-radius: 1000px;
}

.tm-preloader span::before {
  height: 190%;
  width: 190%;
  margin-left: -28.5px;
  margin-top: -28.5px;
  border-width: 5px;
  border-style: solid;
  border-color: #ffffff transparent;
  animation: tmSpinReverse 1s linear infinite;
}

.tm-preloader span::after {
  height: 250%;
  width: 250%;
  margin-left: -37.5px;
  margin-top: -37.5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #ffffff;
  animation: tmSpin 1s linear infinite;
}

.tm-preloader .tm-button {
  position: absolute;
  left: auto;
  top: auto;
  right: 15px;
  bottom: 15px;
}

.wrapper {
  position: relative;
}

/**
 * Button
 */
.tm-button,
a.tm-button,
button.tm-button {
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1px;
  background: #1cb9c8;
  height: 50px;
  line-height: 48px;
  padding: 0 40px;
  display: inline-block;
  overflow: hidden;
  position: relative;
  color: #ffffff;
  border: 1px solid #1cb9c8;
  z-index: 1;
  vertical-align: middle;
}

.tm-button b,
a.tm-button b,
button.tm-button b {
  position: absolute;
  border-radius: 50%;
  width: 0;
  height: 0;
  display: block;
  background: rgba(0, 0, 0, 0.1);
  z-index: -1;
  transform: translate(-50%, -50%);
  transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
}

.tm-button:hover b,
a.tm-button:hover b,
button.tm-button:hover b {
  width: 500px;
  height: 500px;
}

.tm-button.tm-button-dark,
a.tm-button.tm-button-dark,
button.tm-button.tm-button-dark {
  background: #333333;
  border-color: #333333;
}

.tm-button.tm-button-dark b,
a.tm-button.tm-button-dark b,
button.tm-button.tm-button-dark b {
  background: rgba(255, 255, 255, 0.1);
}

.tm-button.tm-button-white,
a.tm-button.tm-button-white,
button.tm-button.tm-button-white {
  background: #ffffff;
  border-color: #ffffff;
  color: #1cb9c8;
}

.tm-button.tm-button-white b,
a.tm-button.tm-button-white b,
button.tm-button.tm-button-white b {
  background: rgba(28, 185, 200, 0.1);
}

.tm-button.tm-button-sm,
a.tm-button.tm-button-sm,
button.tm-button.tm-button-sm {
  font-weight: 500;
  letter-spacing: 0;
  font-size: 16px;
  height: 44px;
  line-height: 42px;
  padding: 0 25px;
}

@media only screen and (max-width: 767px) {
  .tm-button,
  a.tm-button,
  button.tm-button {
    font-weight: 500;
    letter-spacing: 0;
    font-size: 16px;
    height: 44px;
    line-height: 42px;
    padding: 0 25px;
  }
}

.tm-buttongroup {
  margin-left: -10px;
  margin-top: -10px;
}

.tm-buttongroup .tm-button {
  margin-left: 10px;
  margin-top: 10px;
}

/* Read More Button */
.tm-readmore,
a.tm-readmore,
button.tm-readmore {
  display: inline-block;
  color: #444444;
  font-weight: 600;
}

.tm-readmore i,
a.tm-readmore i,
button.tm-readmore i {
  font-size: 12px;
  padding-left: 0;
  transition: padding 0.3s ease-in-out 0s;
}

.tm-readmore:hover,
a.tm-readmore:hover,
button.tm-readmore:hover {
  color: #1cb9c8;
}

.tm-readmore:hover i,
a.tm-readmore:hover i,
button.tm-readmore:hover i {
  padding-left: 5px;
}

/* Video Button */
.tm-videobutton,
a.tm-videobutton,
button.tm-videobutton {
  display: inline-block;
  position: relative;
  z-index: 1;
}

.tm-videobutton a,
a.tm-videobutton a,
button.tm-videobutton a {
  display: inline-block;
  vertical-align: middle;
  border-radius: 999px;
}

.tm-videobutton::before, .tm-videobutton::after,
a.tm-videobutton::before,
a.tm-videobutton::after,
button.tm-videobutton::before,
button.tm-videobutton::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: 100%;
  margin-left: -30px;
  margin-top: -30px;
  border-radius: 999px;
}

.tm-videobutton::before,
a.tm-videobutton::before,
button.tm-videobutton::before {
  background: rgba(255, 255, 255, 0.7);
  z-index: -1;
  animation: tmGrowShrink 1.2s ease-in-out 0s infinite both;
}

.tm-videobutton::after,
a.tm-videobutton::after,
button.tm-videobutton::after {
  background: rgba(28, 185, 200, 0.7);
  z-index: -2;
  animation: tmGrowShrink 1.2s ease-in-out 0.3s infinite both;
}

.tm-videobutton span,
a.tm-videobutton span,
button.tm-videobutton span {
  display: inline-block;
  height: 60px;
  width: 60px;
  line-height: 60px;
  text-align: center;
  background: #ffffff;
  border-radius: 999px;
  color: #1cb9c8;
  transition: all 0.3s ease-in-out 0s;
}

.tm-videobutton span i,
a.tm-videobutton span i,
button.tm-videobutton span i {
  font-size: 26px;
  vertical-align: middle;
  padding-left: 4px;
}

.tm-videobutton:hover span,
a.tm-videobutton:hover span,
button.tm-videobutton:hover span {
  background: #1cb9c8;
  color: #ffffff;
}

/**
 * Title
 */
.tm-section-title {
  margin-bottom: 60px;
}

.tm-section-title h2,
.tm-section-title h3 {
  text-transform: uppercase;
  position: relative;
  font-weight: 700;
  margin-top: -5px;
}

.tm-section-title h2::before,
.tm-section-title h3::before {
  content: attr(data-text);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 200%;
  white-space: nowrap;
  color: #333333;
  opacity: 0.07;
  font-weight: 700;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .tm-section-title {
    margin-bottom: 50px;
