/**
 * Demo Item
 */
.tm-demoitem {
  display: block;
  text-align: center;
  position: relative;
}

.tm-demoitem-image {
  display: block;
  position: relative;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.24);
  transition: all 0.4s ease-in-out 0s;
  overflow: hidden;
}

.tm-demoitem-image::before {
  content: "\f064";
  font-family: 'fontAwesome';
  position: absolute;
  left: 50%;
  top: 50%;
  text-align: center;
  font-size: 18px;
  height: 70px;
  width: 70px;
  line-height: 70px;
  margin-left: -35px;
  margin-top: -35px;
  border-radius: 1000px;
  background: #ffffff;
  color: #1cb9c8;
  z-index: 2;
  transform: translateY(20px);
  transition: all 0.4s ease-in-out 0s;
  visibility: hidden;
  opacity: 0;
}

.tm-demoitem-image::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: #1cb9c8;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease-in-out 0s;
  z-index: 1;
}

.tm-demoitem-image img {
  width: 100%;
}

.tm-demoitem-image .newbadge {
  position: absolute;
  left: auto;
  right: -54px;
  top: -15px;
  background: #1cb9c8;
  z-index: 3;
  color: #ffffff;
  padding: 26px 50px 4px 50px;
  transform: rotate(45deg);
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 700;
  box-shadow: 0 7px 10px rgba(255, 255, 255, 0.2);
}

.tm-demoitem h6 {
  margin-top: 20px;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 500;
}

.tm-demoitem h6 a {
  color: #333333;
  transition: all 0.4s ease-in-out 0s;
}

.tm-demoitem h6 a:hover {
  color: #1cb9c8;
}

.tm-demoitem:hover .tm-demoitem-image {
  transform: translateY(-8px);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25);
}

.tm-demoitem:hover .tm-demoitem-image::before {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tm-demoitem:hover .tm-demoitem-image::after {
  opacity: 0.85;
  visibility: visible;
}

/**
 * Feature
 */
.tm-feature {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.17);
