/**
 * Header
 */
.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 5;
  transition: all 0.4s ease-in-out 0s;
}

.header-inner {
  padding: 15px 0;
}

.header-logo {
  display: block;
}

.header-logo a {
  display: inline-block;
  height: 60px;
}

.header-logo a img {
  height: 100%;
}

.header-navigation {
  text-align: right;
}

.header-navigation ul {
  padding-left: 0;
  margin-bottom: 0;
}

.header-navigation ul li {
  display: inline-block;
  list-style: none;
  margin: 0 15px;
}

.header-navigation ul li:first-child {
  margin-left: 0;
}

.header-navigation ul li:last-child {
  margin-right: 0;
}

.header-navigation ul li a {
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
}

.header.sticky-active {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 99;
  background: rgba(0, 0, 0, 0.85);
}

.header.sticky-active .header-logo a {
  height: 40px;
}

@media only screen and (max-width: 575px) {
  .header-logo {
    text-align: center;
    margin-bottom: 15px;
  }
  .header-logo a {
    height: 40px;
  }
  .header-navigation {
    text-align: center;
  }
}

